import React from 'react';
import { Row } from 'reactstrap';
import { Colxx } from '../../../components/common/CustomBootstrap';

const Filemanager = () => (
  <>
    <Row>
      <Colxx xxs="12" className="mb-0">
        <iframe
          src="/minio"
          title="filemanager"
          style={{ height: '73vh', width: '100%', border: 'none' }}
        />
      </Colxx>
    </Row>
  </>
);
export default Filemanager;
